import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable()
export class ShowHideUiService {
    
    nameBefore: string = localStorage.getItem('username') ? localStorage.getItem('username') : "Welcome!";
    typeBefore: string = localStorage.getItem('type') ? localStorage.getItem('type') : "";
    
    private loggedIn: BehaviorSubject<any> = new BehaviorSubject<any>({name: this.nameBefore, type: this.typeBefore});
    currentUser = this.loggedIn.asObservable();
    
    constructor() {}
    
    setLoggedInUser(value: any) {
        this.loggedIn.next(value);
    }
    
}



